$xs: 320px;
$sm: 576px;
$md: 768px;
$lg: 1024px;
$xl: 1314px;
$xxl: 1440px;
$xxxl: 1606px;

@mixin xs($name, $value) {
  @media screen and (min-width: $xs) {
    #{$name}: $value;
  }
}

@mixin xs-block() {
  @media screen and (min-width: $xs) {
    @content;
  }
}

@mixin sm($name, $value) {
  @media screen and (min-width: $sm) {
